.container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 5;
}

.content {
    border-radius: 40px;
    width: 720px;
    padding: 40px 40px 60px 40px;
    background-color: #1C1C21;
    z-index: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative
}

.closeIcon {
    position: absolute;
    top: 60px;
    right: 40px;
    cursor: pointer;
}

.title {
    align-self: flex-start;
}